import React, { useEffect, useState } from 'react';
import { fetchStudents, addStudent, removeStudent } from './actions/actions'
import {connect} from 'react-redux'
import { Link } from 'react-router-dom'
import "./style.css"


const Students = ({ getStudents, fetchStudents, match, removeStudent, addStudent}) => {

    const id = match.params.id

    useEffect(()=>{
        fetchStudents(id)
    }, [id])


    const [state, setState] = useState('')

    const handleChange = e => console.log(e.target.value)

    console.log(state)

    const handleClick = () => {
        const [name, surname, fathersname] = state.split('')
        const obj = {name, surname, fathersname}
        addStudent(obj)
    }
    

    return( 
        <div>
            <ul>
                {getStudents.map(i => 
                    <li key = {i.id}>
                        <p>
                            {i.name} {i.surname} {i.fathersname}
                        </p>
                        <button type = "button" onClick = {()=>removeStudent(i.id)}>REMOVE</button>
                    </li>
                )
                }
            </ul>

            <input placeholder = "enter new student" type="text" value = {state}  onChange = {()=>handleChange} >
            </input>
            <button type = "button" onClick = {()=>handleClick}>ADD</button>
        </div>
    );
}

const mapStateToProps = state => ({
    getStudents: state.getStudents
  })
  
  
const mapDispatchToProps = {fetchStudents, addStudent, removeStudent}



export default connect(mapStateToProps, mapDispatchToProps)(Students)